@import url(https://fonts.googleapis.com/css?family=Open+Sans&display=swap);
:root {
  margin: 0;
  padding: 0; }

body {
  font-family: "Open Sans", sans-serif;
  font-size: 16px; }

.card {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  -webkit-transition: 0.3s;
  transition: 0.3s;
  border-radius: 5px; }
  .card img {
    border-radius: 5px 5px 0 0; }
  .card .container {
    padding: 2px 16px; }

.card:hover {
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2); }

.responsive-img {
  max-width: 300px; }

.container-wrapper {
  display: grid;
  grid-gap: 5px;
  grid-template-columns: repeat(auto-fit, minmax(100px, 500px));
  grid-auto-rows: 350px; }

nav ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  border: 1px solid #97f3eb;
  background-color: rgba(151, 243, 235, 0.9);
  box-shadow: 0 4px 0 0 rgba(0, 0, 0, 0.1); }

nav li {
  float: left;
  padding: 15px 15px; }
  nav li .apptitle {
    font-size: 18px; }

nav li a {
  border-radius: 5px;
  color: #666;
  font-size: 14px;
  font-weight: bold;
  text-align: center;
  padding: 10px 10px;
  text-decoration: none; }

nav li a:hover:not(.apptitle) {
  background-color: #ddd; }

nav li a.active {
  color: white;
  background-color: #4caf50; }

@media screen and (max-width: 650px) {
  nav ul li {
    text-align: center;
    float: none; } }

footer {
  margin-top: 15px;
  width: 100%;
  background-color: rgba(151, 243, 235, 0.9);
  padding: 1px;
  text-align: center;
  color: #666;
  border: 1px solid #97f3eb; }

@media screen and (max-width: 650px) {
  footer {
    width: 100%; } }

@media screen and (max-width: 480px) {
  footer {
    width: 100%; } }

.container-wrapper h2 {
  text-align: center;
  position: absolute;
  width: 100%; }

